import React from "react";

import { BaseComponent } from "../../base/js";
import { Text } from "../../text/js";

import { propTypes } from "./propTypes";

import '../styles/index.scss';

export const Cards = ({ flexDisplay, card }) => {
    const date = new Date(card.isoDate).toDateString();

    return (
        <BaseComponent flexDisplay={ flexDisplay }>
            <div className="cmp-cards">
                <a target='_blank' href={ card.link } rel='noopener noreferrer'>
                    <div className="card">
                        <div className="card_image">
                            <Text className={'title'} text={ card.title }/>
                            <Text text={ date }/>
                            { card.creator && <Text className={'author'} text={ card.creator }/> }
                        </div>
                    </div>
                </a>
            </div>
        </BaseComponent>
    )
};

Cards.propTypes = propTypes;