import React, { useState } from "react";
import { TextIcon } from "../../text-icon/js";

import { faLayerGroup, faSun, faComments, faChartArea } from '@fortawesome/free-solid-svg-icons'

import '../styles/index.scss';


export const FloatingAction = () => {
    const [active, setActive] = useState(false);

    return (
        <div className='cmp-floatingaction'>
            <button onClick={() => setActive(!active)} className={`action-button ${active ? 'active' : ''}`}>
                <TextIcon icon={faLayerGroup} />
            </button>
            <ul className='actions'>
                <li><a rel='noopener noreferrer' target={'_blank'}
                    href={'https://sullivantobias.github.io/CryptoTracker'}><TextIcon icon={faChartArea} /> </a></li>
                <li><a rel='noopener noreferrer' target={'_blank'}
                    href={'https://sullivantobias.github.io/weather'}><TextIcon icon={faSun} /> </a></li>
                <li><a rel='noopener noreferrer' target={'_blank'}
                    href={'https://sullivantobias.github.io/chat'}><TextIcon icon={faComments} /> </a></li>
            </ul>
        </div>
    )
};
