import React, { useEffect, useState } from "react";
import RSSParser from 'rss-parser/dist/rss-parser.min';

import { BaseComponent } from "../../base/js";
import { Cards } from "../../cards/js";

import { propTypes } from "./propTypes";

import '../styles/index.scss';

export const RssFeed = ({ flexDisplay, url, maxDisplayed }) => {
    const [feedList, setFeedList] = useState({});
    const parser = new RSSParser();

    const CORS_PROXY = "https://cors-anywhere.herokuapp.com/";

    useEffect(() => {
        parser.parseURL(CORS_PROXY + url).then(fd => {
            return { items: fd.items.splice(0, maxDisplayed) }
        })
            .then(data => {
                const feed = data.items.map((item, index) =>
                    <li className={ 'flex-xs-12 flex-md-6 flux-item' } key={ index }>
                        <Cards card={ item }/>
                    </li>
                );

                setFeedList(feed);
            })
            .catch(err => {
                throw err
            });

    }, [maxDisplayed, url]);

    return (
        <BaseComponent flexDisplay={ flexDisplay }>
            <ul className={ 'cmp-flux flex-row' }>{ Object.keys(feedList).length && feedList } </ul>
        </BaseComponent>
    )
};

RssFeed.propTypes = propTypes;
